.container {
  position: relative;
  display: inline-block;
}

.button {
  cursor: pointer;
  padding: 6px 12px;
  font-size: 1rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: white;
  display: flex;
  align-items: center;
  gap: 8px;
}

.caret {
  margin-left: auto;
}

.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 4px;
  padding: 0;
  list-style: none;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 6px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  z-index: 1000;
  min-width: 140px;
}

.dropdownItem {
  margin: 0;
}

.dropdownLink {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.dropdownLink:hover,
.dropdownLink:focus {
  background-color: #f0f0f0;
  outline: none;
}
